import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

import {BrowserRouter} from 'react-router-dom';

import {createStore} from 'redux';      // importas norint sukurt bendr projekto state saugykl
import {Provider} from 'react-redux';   // importas norint paduot saugykl projektui
import reducer from './store/reducer';  // importas su funkcija pakeisti saugyklos state


const store = createStore(reducer);     // sukuriam nauj bendr state saugykl

ReactDOM.render((
    <Provider store={store}>
        <BrowserRouter>
            <App/>
        </BrowserRouter>
    </Provider>), document.getElementById('root')); // reikia "apvilkt" App su <Provider />
                                                    // ir per provider perduot store (bendr saugykl)
registerServiceWorker();



