import React, { Component } from 'react';
import Knopf from '../../Knopf/Knopf';
import Search from '../../Search/Search';

import { connect } from 'react-redux';  // importas norint prijungti bendr saugykl prie Component
import * as filterActions from '../../store/filterActions'; // ia kaip Java enum. ivardini, kad nepadarytum raymo klaid jei pvz naudotum String

class subBottom extends Component {

    render () {
        return (
            <div>
                <Knopf spausti="All books" onClick={this.props.onAllBooks} />
                <Knopf spausti="Most Recent" onClick={this.props.onMostRecent} />
                <Knopf spausti="Most Popular" onClick={this.props.onMostPopular} />
                <Knopf spausti="Free Books" onClick={this.props.onFreeBooks} />
                <Search onClick={this.props.onSearched} />
            </div>                          // onClick ikvieia tam tikr dispatch
        );
    }
}

const mapStateToProps = state => {  // funkcija kuri perkopijuoja state i bendros saugyklos  vietin props
                                    // (galima prie "fltr: state.filter" pridt daugiau kintamj, jeigu j bt reducer.js)
    return {
        fltr: state.filter      
    };
};

const mapDispatchToProps = dispatch => {        
    return {
        onAllBooks: () => dispatch({type: filterActions.ALL_BOOKS}),    // dispatch yra funkcija ikviesti reducer.js, kad pakeist state.
                                                                        // galima po "type: filterActions.ALL_BOOKS" po kablelio perduot reikmi, jeigu reikia. Ir reducer.js atitinkamai apdoroti. Pvz isaugot  masyv kak.
        onMostRecent: () => dispatch({type: filterActions.MOST_RECENT}),
        onMostPopular: () => dispatch({type: filterActions.MOST_POPULAR}),
        onFreeBooks: () => dispatch({type: filterActions.FREE_BOOKS}),
        onSearched: () => dispatch({type: filterActions.SEARCHED_BOOKS}),
    }
};

export default connect(mapStateToProps, mapDispatchToProps) (subBottom);    // prijungia funkcijas (mapStateToProps, mapDispatchToProps) prie komponento



